<script>
import Vue from 'vue'
import { Toast } from 'vant'
import { minepool, minepoollog, addminepool } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
Vue.use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      languageStatus: '',
      url: this.GLOBAL.domainName,
      activeBtn: 1,
      parameter: {
        u_id: '2', // 用户ID
        activity: 'pos' // 固定值（newcoin）
      },
      oreList: [],
      curHeight: '',
      isShow: false,
      ac_method: '理财模式', // 理财模式（'活期模式','定期模式'）
      addCoin: {
        u_id: '2', // 用户ID
        activity: 'pos', // 固定值（newcoin）
        activity_id: '', // 活动ID
        number: '', // 购买数量
        paypass: '' // 交易密码
      },
      infoList: [],
      title: 'Pos矿池'
    }
  },
  created () {
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    console.log(token, '123456')
    this.parameter.token = token
    this.voteList(this.parameter)
    this.userInfos(this.parameter)
    console.log(this.parameter)
    this.beforeMount()
    this.getLanguageStatus()
  },
  methods: {
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    showBox () {
      this.isShow = false
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
      // this.curHeight = h - height // 减去页面上固定高度height
    },
    showVent (activityid, acmethod) {
      this.isShow = true
      this.ac_method = acmethod
      this.addCoin.activity_id = activityid
    },
    // 点击切换按钮1
    changBtn (type) {
      this.activeBtn = type
    },
    voteList (parameter) {
      var that = this
      minepool(parameter).then(res => {
        console.log(res, that)
        that.oreList = res.data
      }).catch((err) => console.log(err))
    },
    userInfos (parameter) {
      var that = this
      minepoollog(parameter).then(res => {
        console.log(res, '明细列表')
        that.infoList = res.data
      }).catch((err) => console.log(err))
    },
    userSub () {
      var that = this
      if (this.addCoin.number === '') {
        that.$toast({
          message: '购买数量不能为空',
          duration: 1500
        })
        return
      }
      if (this.addCoin.paypass === '') {
        that.$toast({
          message: '交易密码不能为空',
          duration: 1500
        })
        return
      }
      var data = objKeySort(this.addCoin)
      var token = sgin(data)
      this.addCoin.token = token
      addminepool(this.addCoin).then(res => {
        console.log(res, that, '交易成功')
        if (res.code === 0) {
          that.$toast({
            message: '参与成功',
            duration: 1500
          })
          that.isShow = false
          this.addCoin.number = ''
          this.addCoin.paypass = ''
          delete this.addCoin.token
        }
        // this.datas = res.data
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        that.isShow = false
        this.addCoin.number = ''
        this.addCoin.paypass = ''
        delete this.addCoin.token
      })
    }
  }
}
</script>
